* {
  margin: 0;
  padding: 0;
  overflow: visible;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header-navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.img {
  background: url(../img/Fon.jpg) no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 48px;
  min-height: 100px;
}

.header .logotype {
  font-weight: bold;
}

.header .btn {
  position: relative;
  text-decoration: none;
  color: black;
  margin: 0 0 6px 20px;
  font-weight: bold;
}

.header .btn:hover:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #97c89a;
}

.header .btn:nth-child(1) {
  padding: 0;
}

.header .btn:nth-child(5) {
  margin: 0 0 0 53px;
}

.main {
  border-top: 1px;
}

.gradient {
  padding-top: 184px;
  background: linear-gradient(to top right, #267ebe, #6dd5ed);
}

.main_colortext {
  color: #ffffff;
  margin-bottom: 80px;
  font-size: 60px;
}

.my-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feedback-card {
  background-color: #ffffff;
  border-radius: 10px;
  margin-bottom: 45px;
  padding-top: 50px;
  padding: 0 33px;
  padding-bottom: 75px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
}

.feedback-card__face {
  margin: 68px 0;
}

.feedback-card__text {
  margin: 24px 40px 0 0;
  font-weight: 500;
  margin-bottom: 36px;
}

.screen {
  height: 100vh;
}

.features {
  text-align: center;
  margin-bottom: 36px;
}

.centerP {
  text-align: center;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 46px;
}

.advantages {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.advantages-li {
  list-style-type: none;
  padding-top: 115px;
  text-align: center;
  padding-bottom: 14px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  position: relative;
}

.advantages-li + li {
  border-left: 1px solid #dddddd;
}

.advantages-li:before {
  content: '';
  background-image: url(../img/noun_Coffee.png);
  background-color: #eaf4eb;
  background-repeat: no-repeat;
  background-position: center;
  width: 55px;
  height: 55px;
  position: absolute;
  top: 40%;
  left: 50%;
  margin-top: -27.5px;
  margin-left: -27.5px;
  border-radius: 50%;
}

.advantages-li__2:before {
  background-image: url(../img/noun_muffin.png);
}

.advantages-li__3:before {
  background-image: url(../img/noun_wifi.png);
}

.advantages-li__4:before {
  background-image: url(../img/noun_Sofa.png);
}

@media screen and (max-width: 960px) {
  .feedback-card {
    width: calc(50% - 20px);
  }

  .advantages-li {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .feedback-card {
    width: calc(50% - 20px);
  }

  .advantages-li {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .feedback-card {
    width: calc(100% - 20px);
  }

  .advantages-li {
    width: calc(100% - 20px);
  }
}